
Template.__checkName("doc");
Template["doc"] = new Template("Template.doc", (function() {
  var view = this;
  return HTML.Raw("<div class=\"ui container\">\n		<div class=\"row\">\n			<br>\n			<h1>Dokumentation</h1>\n			<br>\n			<h2 class=\"ui dividing header\">\n				Start\n			</h2>\n			<p>\n				Auf der Startseite findest du neben einer einführenden Erklärung der zentralen Funktionen zwei Top 5-Listen, welche die beliebtesten Begriffe anzeigen - deine persönlichen sowie die ingesamt beliebtesten Begriffe.\n			</p>\n			<p>\n				Über die Feedback-Funktion hast du die Möglichkeit direkt mit uns in Kontakt zu treten. Alternativ oder falls es nicht ganz so dringend ist kanst du dich auch am Ende des Testzeitraums in einem Fragebogen äußern. Deine\n				Anfragen werden vertraulich behandelt und nur im Zusammenhang mit der Nutzerstudie ausgewertet.\n			</p>\n			<p>\n				Am Ende des Testzeitraums wird ein Fragebogen freigeschalten - über die Startseite wirst du darüber informiert.\n			</p>\n			<br>\n			<h2 class=\"ui dividing header\">\n				Trainer\n			</h2>\n			<p>\n				Im Trainer kannst du über das Menü 'Einstellungen' drei verschiedene Optionen wählen - 'Methode', 'Quelle' und 'Reihenfolge'.\n			</p>\n			<br>\n			<h3>Methode</h3>\n			<h3><i>Lesen</i></h3>\n			<p>\n				Hier kannst du die Begriffe und ihre Bedeutungen ansehen, über das Herzsymbol favorisieren und aus der Favoritenliste löschen. Anders als im Register wird hier jeder Begriff separat dargestellt.\n			</p>\n			<h3><i>Rate Wort</i></h3>\n			<p>\n				Mit aktivierter Methode 'Rate Wort' werden lediglich die Definitionen angezeigt. Mit einem Click auf 'Lösung anzeigen' wird der korrekte Begriff dargestellt.\n			</p>\n			<h3><i>Rate Definition</i></h3>\n			<p>\n				Mit aktivierter Methode 'Rate Definition' wird hingegen der Begriff angezeigt. Mit einem Click auf 'Lösung anzeigen' werden die korrekten Definitionen dargestellt.\n			</p>\n			<h3><i>Eingabetest</i></h3>\n			<p>\n				Mit aktivierter Methode 'Eingabetest' werden die Definitionen des gesuchten Begriffes angezeigt und die hast die Möglichkeit über die Tastatur in das Textfeld den gesuchten Begriff einzugeben. Mit jeder Eingabe aktualisiert sich die Lösungsanzeige, korrekt\n				gesetzte Buchstaben werden als solche angezeigt, falsch gesetzte Buchstaben erscheinen als Unterstrich ('_'). Ist das komplette Wort korrekt eingegeben, erscheint eine Erfolgsmeldung und der nächste Begriff wird geladen.\n			</p>\n			<p>\n				Mit einem Click auf das Fragezeichen wird die Lösung angezeigt. Daraufhin erfolgt keine automatische Weiterleitung zum nächsten Begriff.\n			</p>\n			<br>\n			<h3>Quelle</h3>\n			<h3><i>Alle</i></h3>\n			<p>\n				Alle im Register enthaltenen Begriffe stehen zur Verfügung.\n			</p>\n			<h3><i>Favoriten</i></h3>\n			<p>\n				Ausschließlich favorisierte Begriffe stehen zur Verfügung.\n			</p>\n			<h3><i>Alle ohne Favoriten</i></h3>\n			<p>\n				Alle im Register enthaltenen Begriffe ohne der favorisierten Begriffe stehen zur Verfügung.\n			</p>\n			<br>\n			<h3>Reihenfolge</h3>\n			<h3><i>Zufall</i></h3>\n			<p>\n				Begriffe werden zufällig aus der gewählten Quelle entnommen und die Browse-Möglickeit ist auf den Vor-Button reduziert.\n			</p>\n			<h3><i>Alphabet</i></h3>\n			<p>\n				Vom aktuellen Begriff ausgehend werden die Folgebegriffe in alphatetischer Reihenfolge entsprechend der gewählten Quelle abgerufen.\n			</p>\n			<br>\n			<h2 class=\"ui dividing header\">\n				Register\n			</h2>\n			<p>\n				Im Register werden alle eingetragenen Begriffe alphabtisch sortiert aufgelistet. Du hast du Möglichkeit Begriffe zu favorisieren, von der Favoritenliste zu entfernen oder auch nach Begriffen zu suchen. Sucheingaben müssen mit der Enter-Taste bestätigt\n				werden.\n				<br> Es werden sämtliche Eintragsrelevanten Inhalte dursucht, d.h. Wort, Definition und Wortart, sodass der Suchbegriff 'Adjektiv' etwa alle Adjektive liefert.\n			</p>\n			<h2 class=\"ui dividing header\">\n					Kompaktmodus\n				</h2>\n			<p>\n				Der Kompaktmodus bietet eine alternative Benutzeroberfläche zu der des Trainers. <br>\n				Die Menüstruktur ist identisch, allerdings auf Switch-Buttons reduziert. <br>\n				Die Browse-Buttons sind umfassen vollflächig die gesamte zur Verfügung stehende Höhe des Browserfensters,\n				Die Schriftgrößen passen sich dynamisch an die Bildschirmgröße an.\n				Funktional unterscheiden sich der Standard Trainermodus und der Kompaktmodus darin, dass im Kompaktmodus\n				die Methode 'Eingabetest' fehlt. <br>\n				Befindet man sich im Standard Trainermodus mit gewählter Methode 'Eingabetest' und wechselt in den Kompaktmodus,\n				so wird standardmäßig auf die Methode 'Lesen' gewechselt.\n			</p>\n		</div>\n	</div>");
}));
