
Template.__checkName("docWidget");
Template["docWidget"] = new Template("Template.docWidget", (function() {
  var view = this;
  return [ HTML.DIV("\n		", HTML.Raw("<h2>Erste Schritte</h2>"), "\n		", HTML.Raw("<p>\n			Die Fremdworttrainer-App verfügt über ein Wortregister und einen Trainer mit 4 Übungsmethoden.\n		</p>"), "\n		", HTML.Raw("<p>\n			Im <b>Register</b> findest du eine Auflistung aller vorhandenen Begriffe. Begriffe können favorisiert oder von der Favoritenliste entfernt werden.\n		</p>"), "\n		", HTML.Raw("<p>\n			Im <b>Trainer</b> kannst du aus 4 verschiedenen Übungsmethoden wählen: 'Lesen', 'Rate Wort', 'Rate Definition' und 'Eingabetest'.\n			<br> Des weiteren hast du die Möglichkeit die Quelle der Wörter auszuwählen. Entweder das komplette Register ('Alle'),\n			nur favorisierte Begriffe ('Favoriten') oder das komplette Register ohne Favoriten ('Alle ohne Favoriten'). <br>\n			Wörter können sowohl im Register als auch mit aktivierter Methode 'Lesen' sowohl im Trainer als auch im Kompaktmodus zu der Liste der Favoriten hinzugefügt\n			oder entfernt werden.\n		</p>"), "\n		", HTML.Raw("<p>\n			Am Seitenkopf findest du eine Switch-Button-Leiste, mit der du in den <b>Kompaktmodus</b> und wieder zurück wechseln kannst.<br>\n			Der Kompaktmodus bietet dir eine alternative Benutzeroberfläche für den Trainer.\n			Die Methode 'Eingabe' ist im Kompaktmodus nicht vorhanden.\n		</p>"), "\n\n		", HTML.A({
    "class": "ui button primary large btn-question",
    href: function() {
      return Spacebars.mustache(view.lookup("pathFor"), "doc");
    }
  }, "Mehr"), "\n	"), HTML.Raw("\n	<br>") ];
}));
